  .titleBt tr{float: left; text-align: center;display: inline-table;width: 100%; border-bottom: 1px solid #777; height: 2rem;line-height: 2rem;color: #fff; font-size: 0.7rem;}
  .titleBt tr:first-child{margin-top: 0.8rem;}
  .titleBt tr:last-child{border-bottom: 0}
  .titleBt tr th{border-right: 1px solid #777;  background: #35120e; }
  .titleBt tr th:last-child{border-right: none;}
  .titleBt tr td{border-right: 1px solid #777;}
  .titleBt tr td:last-child{border-right: none; border-bottom: 0}
</style>



